:root {
  --color-black: #000000;
  --color-darker: rgba(0, 0, 0, 0.24);
  --color-light: #939FAD;
  --color-white: #ffffff;
  --color-primary: #1377c9;
  --color-primary-light: #d0e4f4;
  --color-primary-darker: #0a3e69;
  --color-secondary: #262f36;
  --color-secondary-light: #d4d5d7;
  --color-secondary-darker: #14181c;
  --color-success: #17b15c;
  --color-success-light: #d1efde;
  --color-success-darker: #0c5c30;
  --color-warning: #FFB905;
  --color-warning-light: #fcf4db;
  --color-warning-darker: #7d6928;
  --color-danger: #bb3434;
  --color-danger-light: #f1d6d6;
  --color-danger-darker: #611b1b;
  --color-info: #13c0c9;
  --color-info-light: #d0f2f4;
  --color-info-darker: #0a6469;
  --color-muted: #cccccc;
  --color-gray-darker: #262626;
  --color-gray-dark: #888888;
  --color-dark: #262626;
  --color-gray-light: #f9f9f9;
  --color-link: #3687e4;
  --color-border-color: #efefef;
  /* ... */
}

.border-default {
  border-color: #efefef;
}

.mdk-header {
  position: relative;
  z-index: 1;
  margin-bottom: 1.25rem;
  height: 200px;
}

.mdk-header :last-child {
  margin-bottom: 0;
}

.mdk-header[data-condenses], .mdk-header[data-reveals] {
  will-change: transform;
}

.mdk-header [data-fx-condenses] {
  transform-origin: left top;
  opacity: 0;
}

.mdk-header [data-fx-condenses][data-fx-id] {
  opacity: 1;
}

.mdk-header [data-primary] {
  z-index: 1;
}

.mdk-header__bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

[data-effects*="parallax-background"] .mdk-header__bg {
  visibility: hidden;
}

.mdk-header__bg-rear,
.mdk-header__bg-front {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  background-size: cover;
}

.mdk-header__bg-rear {
  opacity: 0;
}

.mdk-header__content {
  position: relative;
  width: 100%;
  height: 100%;
}

.mdk-header[data-condenses] .mdk-header__content {
  z-index: 0;
}

.mdk-header[data-condenses] [data-primary] {
  z-index: 1;
}

.mdk-header--shadow::after {
  position: absolute;
  right: 0;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 5px;
  content: "";
  pointer-events: none;
  opacity: 0;
  box-shadow: inset 0px 5px 6px -3px rgba(0, 0, 0, 0.4);
  will-change: opacity;
  z-index: -1;
}

.mdk-header--shadow-show::after {
  opacity: 1;
}

.mdk-header--fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  margin-bottom: 0;
}

.mdk-header[disabled], .mdk-header[disabled]::after,
.mdk-header[disabled] .mdk-header__bg-rear,
.mdk-header[disabled] .mdk-header__bg-front,
.mdk-header[disabled] .mdk-header__content > [data-primary] {
  transition: none !important;
}

.mdk-header-layout {
  height: 100%;
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.mdk-header-layout--fullbleed {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.mdk-header-layout > .mdk-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.mdk-header-layout[data-fullbleed] > .mdk-header, .mdk-header-layout[data-has-scrolling-region] > .mdk-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.mdk-header-layout__content {
  position: relative;
  z-index: 0;
}

.mdk-header-layout__content--fullbleed {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.mdk-header-layout__content--scrollable {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.mdk-box {
  position: relative;
  margin-bottom: 1.25rem;
}

.mdk-box__bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  height: 100%;
}

[data-effects*="parallax-background"] .mdk-box__bg {
  visibility: hidden;
}

.mdk-box__bg-rear,
.mdk-box__bg-front {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  background-size: cover;
}

.mdk-box__bg-rear {
  opacity: 0;
}

.mdk-box__content {
  position: relative;
  width: 100%;
  height: 100%;
}

.mdk-box[disabled], .mdk-box[disabled]::after,
.mdk-box[disabled] .mdk-box__bg-rear,
.mdk-box[disabled] .mdk-box__bg-front,
.mdk-box[disabled] .mdk-box__content > [data-primary] {
  transition: none !important;
}

.mdk-drawer {
  position: relative;
  visibility: hidden;
}

.mdk-drawer[data-opened], .mdk-drawer[data-closing] {
  visibility: visible;
}

.mdk-drawer[data-persistent] {
  width: 256px;
}

.mdk-drawer[data-persistent][data-position=left] {
  /*!rtl:ignore*/
  right: auto;
}

.mdk-drawer[data-persistent][data-position=right] {
  /*!rtl:ignore*/
  left: auto;
}

.mdk-drawer__content {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 256px;
  transition: transform 0.2s ease;
  background-color: #fff;
  /*!rtl:begin:ignore*/
  left: 0;
  transform: translate3d(-100%, 0, 0);
  /*!rtl:end:ignore*/
}

[data-position=right] > .mdk-drawer__content {
  /*!rtl:begin:ignore*/
  right: 0;
  left: auto;
  transform: translate3d(100%, 0, 0);
  /*!rtl:end:ignore*/
}

[data-opened] > .mdk-drawer__content {
  transform: translate3d(0, 0, 0);
}

.mdk-drawer__scrim {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transition: opacity 0.2s ease;
  transform: translateZ(0);
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

[data-opened] > .mdk-drawer__scrim {
  opacity: 1;
}

[data-persistent] > .mdk-drawer__scrim {
  visibility: hidden;
  opacity: 0;
}

.mdk-drawer-layout {
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
}

.mdk-drawer-layout--fullbleed {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.mdk-drawer-layout .mdk-drawer {
  position: relative;
}

.mdk-drawer-layout .mdk-drawer[data-persistent] {
  width: 256px;
}

.mdk-drawer-layout .mdk-drawer[data-persistent][data-position=left] {
  /*!rtl:remove*/
  order: 0;
  /*rtl:raw:
        order: 2;
        */
}

.mdk-drawer-layout .mdk-drawer[data-persistent][data-position=right] {
  /*!rtl:remove*/
  order: 2;
  /*rtl:raw:
        order: 0;
        */
}

.mdk-drawer-layout__content {
  position: relative;
  height: 100%;
  transition: transform 0.2s;
  min-width: 320px;
  z-index: 0;
  order: 1;
  flex: 1 1 0%;
}

.mdk-drawer-layout__content--scrollable {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.mdk-reveal {
  z-index: 0;
  position: relative;
  overflow: hidden;
  display: block;
}

.mdk-reveal[data-opened] .mdk-reveal__partial, .mdk-reveal--hover:hover .mdk-reveal__partial {
  opacity: 0;
}

.mdk-reveal__content {
  transition: transform .2s;
  background-color: #ffffff;
  position: relative;
  z-index: 0;
}

.mdk-reveal__partial {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  content: '';
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 60%, rgba(255, 255, 255, 0.95) 80%, white 100%);
  opacity: 1;
  z-index: 1;
  transition: .2s opacity;
  pointer-events: none;
}

.mdk-carousel__content {
  transition: transform .4s;
  transform: translate3d(0, 0, 0);
  touch-action: pan-x;
}

.mdk-carousel__item {
  float: left;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
  -webkit-touch-callout: none;
  width: 100%;
}

.mdk-tooltip {
  transform: scale(0);
  transition: transform .2s;
  transform-origin: top center;
  will-change: transform;
  display: inline-block;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  font-size: 13px;
  z-index: 999;
  font-weight: 500;
  text-align: center;
  position: fixed;
  line-height: 1;
  max-width: 200px;
  padding: .4rem;
  border-radius: .25rem;
  pointer-events: none;
}

.mdk-tooltip[data-opened] {
  transform: scale(1);
}

